.address {
	&_list {
		padding-top: 20px;
	}
	&_item {
		position: relative;
		margin-bottom: 16px;
		padding: 15px 25px;
		font-size: 13px;
		border: 1px solid $border-color;
		border-radius: 10px;
		color: $six-color;
		&:last-child {
			margin-bottom: 0;
		}
		h6,
		p {
			@include lineht(28px);
		}
		h6 {
			font-weight: bold;
		}
		p {
			.name {
				float: left;
				width: 60px;
				margin-right: 24px;
				text-align: right;
				color: $nine-color;
			}
		}
	}
	&_editor {
		position: absolute;
		top: 50px;
		right: 50px;		
		a {
			margin: 0 6px;
			&:hover {
				color: $main-color;
			}
		}
	}
	&_btm {
		padding: 20px 0;
		text-align: center;
	}
	&_add {
		display: inline-block;
		height: 40px;
		line-height: 38px;
		padding: 0 26px;
		font-size: 14px;
		border: 1px solid $border-color;
		border-radius: 8px;
		color: $six-color;
		background-color: $bg-color;
		transition: all .1s ease-in-out;
		cursor: pointer;
		i {
			transition: all .1s ease-in-out;
			margin-right: 6px;
			color: $main-color;
		}
		&:hover {
			color: $white-color;
			background-color: $main-color;
			border-color: $main-color;
			i {
				color: $white-color;
			}
		}
	}
	&_model {
		position: relative;
		top: 50%;
		@include size(600px,400px);
		margin-top: -200px;
		margin-left: auto;
		margin-right: auto;
		padding: 30px 30px 20px;
		background-color: $white-color;
		border-radius: 5px;
		&_close {
			position: absolute;
			right: -18px;
			top: -18px;
			overflow: hidden;
			@include size(36px,36px);
			padding: 2px;
			background-color: $white-color;
			border-radius: 50%;
			cursor: pointer;
			i {
				display: block;
				@include size(32px,32px);
				line-height: 32px;
				text-align: center;
				border-radius: 50%;
				transition: all .2s ease-in-out;
				@include colbg($white-color,$gray-color);
				&:hover {
					background-color: $blue-color;
				}
			}
		}
	}
}