@mixin shadow {
	transition: all .6s;

	&:hover {
		box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
		transform: translate3d(0, -2px, 0);
	}
}

@import 'variable';
@import 'reset';
@import 'mixin';
@import 'mylei';
@import 'header';
@import 'form';

.login {
	&_head {
		height: 90px;
		background-color: $white-color;

		h1 a {
			margin-top: 20px;
		}
	}

	&_banner {
		position: relative;
		height: 440px;
		@extend .bg_cover;
	}

	&_box {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translateY(-50%);
		margin-left: 240px;
		padding: 20px 30px;
		width: 360px;
		border-radius: 15px;
		background-color: rgba(255, 255, 255, .9);
	}

	&_title {
		margin-bottom: 20px;
		font-size: 16px;
		text-align: center;
	}
}

.banner {
	&_box {
		position: relative;
		height: 540px;
		overflow: hidden;

		.hd ul {
			position: absolute;
			left: 50%;
			bottom: 30px;
			background-color: rgba(0, 0, 0, .3);
			padding: 6px 10px;
			border-radius: 20px;
			z-index: 3;
		}

		.hd ul li {
			@include size(12px, 12px);
			float: left;
			margin: 0 6px;
			border-radius: 50%;
			background-color: rgba(255, 255, 255, .5);
			cursor: pointer;

			&.on {
				background: $main-color;
			}
		}

		.bd ul li {
			width: 100%;
			height: 540px;
			@extend .bg_cover;
		}
	}
}

.index {
	&_brand {
		padding-top: 20px;
	}

	&_advantage {
		&_list {
			width: 958px;
			margin: 20px 0;

			li {
				float: left;
				margin-right: 10px;
				overflow: hidden;
				@include size(232px, 140px);
				@include shadow;

				&:nth-child(4n+4) {
					margin-right: 0;
				}
			}
		}
	}

	&_notice {
		width: 230px;
	}
}

.common {
	&_title {
		height: 30px;

		&_more {
			margin-top: 2px;
			font-size: 14px;
			color: $nine-color;

			&:hover {
				color: $deputy-color;
			}
		}
	}

	&_box {
		padding-bottom: 30px;
		border-top: 2px solid $main-color;
	}

	&_bg {
		position: fixed;
		left: 0;
		top: 0;
		@include size(100%,100%);
		background-color: rgba(0,0,0,.5);
		z-index: 8;
	}
}

.notice {
	&_list {
		overflow: hidden;
		height: 140px;
		margin: 20px 0;
		padding: 10px;
		box-shadow: 0 1px 2px rgba(0, 0, 0, .1);
		background-color: $white-color;

		li {
			position: relative;
			@extend .text_ellipsis;
			line-height: 30px;
			padding-left: 20px;
			border-bottom: 1px dashed $border-color;

			&:before {
				content: '';
				@include position(8px, 13px);
				display: block;
				@include size(4px, 4px);
				border-radius: 50%;
				background-color: $main-color;
			}

			&:hover {
				background-color: $bg-color;
				color: $main-color;
			}
		}
	}
}

.mid_pic {
	margin: 20px 0;
	@include shadow;
}

.nomore_data {
	line-height: 50px;
	text-align: center;
	color: $nine-color;
}

@import 'goods';
@import 'center';
@import 'order';
@import 'coupon';
@import 'collect';
@import 'address';
@import 'footer';

.data_none {
	text-align: center;
	padding-top: 50px;
	padding-bottom: 20px;
	color: $nine-color;

	p {
		margin-bottom: 10px;

		i {
			font-size: 50px;
			color: $border-color;
		}

		a {
			margin: 0 6px;
			color: $main-color;
		}
	}
}