$foot-color: #616161;
$ft-border-color: #e0e0e0;
.footer {
	&_box {
		background-color: $white-color;
		border-top: 1px solid $ccc-color;
	}
	&_service {
		padding: 30px 0;
		border-bottom: 1px solid $bg-color;
	}
	&_service_item {
		width: 20%;
		float: left;
		color: $foot-color;
		font-size: 16px;
		border-right: 1px solid $ft-border-color;
		text-align: center;
		cursor: pointer;
		.iconfont {
			margin-right: 4px;
			font-size: 20px;
		}
		&:nth-last-child(1) {
			border-right: none;
		}
		&:hover {
			color: $deputy-color;
		}
	}
	&_top {
		padding: 30px 0;
	}
	&_list {
		width: 160px;
		float: left;
		h5 {
			margin-bottom: 16px;
		}
		li {
			@extend .text_ellipsis;
			line-height: 22px;
			font-size: 12px;
			color: $nine-color;
			&:hover {
				color: $deputy-color;
			}
		}
	}
	&_contact {
		float: right;
		width: 240px;
		text-align: center;
		border-left: 1px solid $ft-border-color;
		h6 {
			margin-bottom: 6px;
			font-size: 22px;
			color: $deputy-color;			
		}
		p {
			font-size: 12px;
			margin-bottom: 8px;
			color: $foot-color
		}
		a {
			margin: 0 auto;
			display: block;
			@include size(118px,28px);
			line-height: 26px;
			font-size: 12px;
			border: 1px solid $deputy-color;
			color: $deputy-color;
			transition: all .4s;
			.iconfont {
				font-size: 14px;
			}
			&:hover {
				@include colbg($white-color,$deputy-color);
			}
		}
	}
	&_infor {
		background-color: $bg-color;
		text-align: center;
	}
	&_btm {
		padding: 12px 0;
		font-size: 12px;
		color: $nine-color;
		li {
			display: inline-block;
		}
	}
	&_btm_item {
		padding: 0 2px;
		&:hover {
			color: $deputy-color;
		}
	}
}