.header {
	&_top {
		position: relative;
		height: 30px;
		line-height: 30px;
		font-size: 12px;
		color: $six-color;
		box-shadow: 0 1px 1px $border-color;
		background-color: $bg-color;
		z-index: 10;
	}
	&_top_left {
		a {
			margin-left: 6px;

			&:hover {
				color: $main-color;
			}
		}
		span {
			margin-left: 6px;
			cursor: pointer;

			b {
				color: $main-color;
			}
		}
	}
	&_nav_list {
		&>li {
			float: left;
			color: $ccc-color;
		}
		.header_nav_item {
			color: $tre-color;
		}
	}
	&_nav_item {
		position: relative;
		padding: 0 10px;		
		i {
			position: relative;
			top: 2px;
			margin-left: 4px;
			color: $ccc-color;
		}
		&:hover {
			color: $main-color;
			.header_nav_unit {
				display: block;
				opacity: 1;
			}
		}
		&.white:hover {
			color: inherit;
			background-color: $white-color;
		}
	}
	&_num {
		color: $main-color;
	}
	&_nav_unit {
		display: none;
		opacity: 0;
		@include position(0, 30px);
		width: 100%;
		text-align: center;
		box-shadow: 0 1px 2px $ccc-color;
		transition: all .4s;
		background-color: $white-color;
		color: $six-color;
	}
	&_nav_ewm {
		display: block;
		margin: 6px auto;
		@include size(80px,80px);
	}
	&_nav_cell {
		@extend .text_ellipsis;

		&:hover {
			color: $main-color;
			background-color: $bg-color;
		}
	}
	&_cart {
		position: relative;
		width: 130px;
		height: 40px;
		line-height: 38px;
		margin-top: 10px;
		font-size: 14px;
		color: $main-color;	
		border: 1px solid $ccc-color;
		background-color: $bg-color;
		z-index: 6;
		&:hover {			
			border-bottom: none;
			background-color: $white-color;
			.header_cart_box {
				display: block;
				opacity: 1;
			}
		}
	}
	&_cart_icon {
		display: block;
		text-align: center;
	}
	&_cart_box {
		display: none;
		opacity: 0;
		position: absolute;
		left: -192px;
		top: 40px;
		width: 320px;
		color: $six-color;
		background-color: $white-color;
		box-shadow: 0 1px 2px $ccc-color;
		transition: all .4s;
	}
	&_cart_none {
		padding: 30px 0;
		text-align: center;
	}
	&_cart_project {
		max-height: 280px;
		overflow-y: scroll;
	}
	&_cart_list {
		width: 100%;
	}
	&_cart_item {
		width: 320px;
		margin-bottom: 10px;
		border-bottom: 1px solid $bg-color;
		&:last-child {
			margin-bottom: 0;
		}
		&:hover {
			background-color: $bg-color;
		}
	}
	&_cart_thumb {
		@include size(82px,82px);
		margin:10px;
		border: 1px solid $border-color;
	}
	&_cart_title {
		@include rowhide(2,24px);
		&:hover {
			color: $main-color;
		}
	}
	&_cart_number {
		color: $nine-color;
	}
	&_cart_price {
		font-size: 14px;
		font-weight: bold;
		color: $main-color;
	}
	&_cart_infor {
		width: 160px;
		overflow: hidden;
		margin-top: 10px;
	}
	&_cart_dele {
		width: 40px;
		padding: 20px 10px;
		height: 100%;
		text-align: center;
		color: $white-color;
		background-color: $deputy-color;
		cursor: pointer;
	}
	&_cart_btm {
		height: 41px;
		line-height: 40px;
		border-top: 1px solid $border-color;
		background-color: $bg-color;
		color: $six-color;
		span {
			float: left;
			@include lineht(40px);
			margin-left: 12px;
			b {
				margin: 0 4px;
				color: $main-color;
				strong {
					font-size: 16px;
					font-weight: bold;
				}
			}
		}
	}
	&_cart_btn {
		@include size(100px,40px);
		text-align: center;
		@include colbg($white-color,$main-color);
		&:hover {
			background-color: lighten($main-color,10%);
			color: $white-color;
		}
	}
	&_box {
		padding: 20px 0;
		background-color: $white-color;
	}
	&_left {
		h1 {
			@include size(170px,50px);
			margin-top: 6px;
			font-size: 0;
		}
	}
	&_search {
		margin-left: 180px;
	}
	&_form {
		position: relative;
		width: 500px;
		input {
			@include size(100%,36px);
			line-height: 34px;
			padding-left: 40px;
			border: 2px solid $main-color;
			@include placehold($ccc-color);
			border-radius: 3px;
		}
		i {
			position: absolute;
			left: 12px;
			top: 9px;
			font-size: 20px;
			color: $ccc-color;
		}
		&_btn {
			position: absolute;
			right: 0;
			top: 0;
			@include size(80px,36px);
			text-align: center;
			@include colbg($white-color,$main-color);
		}
	}
	&_keys {
		margin-top: 6px;
		font-size: 12px;
		h5 {
			margin-right: 20px;
			color: $main-color;
		}
		&_item {
			float: left;
			margin-right: 20px;
			color: $nine-color;
			&:hover {
				color: $main-color;
			}
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&_nav {
		height: 40px;
		background-color: $white-color;
	}
	&_nav_title {
		position: relative;
		font-size: 14px;
		z-index: 5;
		a:hover {
			color: $main-color;
		}
		&:hover {
			.header_nav_kind {
				display: block;
			}
		}
	}
	&_nav_btn {
		width: 228px;
		line-height: 40px;
		padding: 0 20px;
		font-size: 16px;
		background-color: $main-color;
		color: $white-color;
		cursor: pointer;
	}
	&_nav_kind {
		display: none;
		@include position(0,40px);
		padding-top: 10px;
		@include size(228px,540px);
		background-color: rgba(255,255,255,.95);
	}
	&_nav_dan {
		padding: 10px;
		border-bottom: 1px solid $bg-color;
		&:last-child {
			border-bottom: none;
		}
		&:hover {
			background-color: $white-color;
			.header_nav_right {
				display: block;
			}
		}
		h4 {
			margin-bottom: 6px;
			font-size: 16px;
		}
	}
	&_nav_classify {
		@extend .clearfix;
		a {
			float: left;
			margin-right: 12px;
			margin-bottom: 4px;
			&.active {
				color: $main-color;
			}
		}
	}	
	&_nav_right {
		display: none;
		position: absolute;
		left: 228px;
		top: 0;
		@include size(710px,540px);
		background-color: $white-color;
	}
	&_nav_show {
		padding: 20px;
		li {
			@extend .clearfix;
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&_nav_tips {
		width: 120px;
		text-align: center;
		float: left;
	}
	&_nav_type {
		width: 540px;
		padding-bottom: 12px;
		float: left;
		border-bottom: 1px solid $bg-color;
		a {
			float: left;
			font-size: 13px;
			margin-right: 12px;
			margin-bottom: 6px;
		}
	}
	&_nav_all {
		line-height: 40px;
		margin-left: 40px;
		li {
			float: left;
			margin-right: 50px;
			font-size: 16px;
			&:hover {
				color: $main-color;
			}
		}
	}
}