.order {
	&_top {
		position: relative;
		&_list {
			@extend .clearfix;
			margin: 0 20px;
			border-bottom: 1px solid $border-color;
			li {
				float: left;
				@include lineht(50px);
				width: 100px;
				cursor: pointer;
				&.active {
					color: $main-color;
				}
			}
		}
	}
	&_more {
		position: absolute;
		right: 20px;
		top: 10px;
		line-height: 30px;
		color: $nine-color;
		&:hover {
			color: $main-color;
		}
		i {
			margin-right: 4px;
		}
	}
	&_search {
		margin: 20px;
		@include lineht(30px);
		$ipt-radius: 3px;
		input {
			display: block;
			width: 200px;
			height: 100%;
			text-indent: 10px;
			border: 1px solid $border-color;
			border-top-left-radius: $ipt-radius;
			border-bottom-left-radius: $ipt-radius;
			@include placehold($nine-color,13px);
		}
		i {
			display: block;
			@include size(60px,100%);
			font-size: 20px;
			text-align: center;
			color: $white-color;
			background-color: $ccc-color;
			border-top-right-radius: $ipt-radius;
			border-bottom-right-radius: $ipt-radius;
			cursor: pointer;
		}
	}
	&_nav {
		&_item {
			display: none;
			opacity: 0;
			margin: 0 20px 20px;
			font-size: 13px;
			color: $six-color;
			&.active {
				display: block;
				opacity: 1;
			}
		}
		&_list {
			@extend .clearfix;
			@include lineht(42px);
			margin: 0 20px 20px;
			background-color: $bg-color;
			li {
				float: left;
				width: 108px;
				color: $six-color;
				text-align: center;
				cursor: pointer;
				&:nth-child(1) {
					width: 280px;
				}
				&:nth-child(2) {
					width: 90px;
				}
				&:nth-child(3) {
					width: 80px;
				}
				&:nth-child(4) {
					width: 120px;
				}
			}
		}
	}
	&_item {
		margin-bottom: 20px;
		border: 1px solid $border-color;
		&:last-child {
			margin-bottom: 0;
		}
		&_top {
			position: relative;
			padding-left: 40px;
			@include lineht(42px);
			border-bottom: 1px solid $border-color;
			background-color: $bg-color;
			a {
				position: absolute;
				left: 56%;
				i {
					margin-right: 8px;
					color: $main-color;
				}
			}
		}
		&_border {
			text-align: center;
			border-left: 1px solid $border-color;
		}
		&_wrap {
			border-bottom: 1px solid $border-color;
			&:last-child {
				border-bottom: none;
			}
			&>p {
				display: table-cell;
				width: 108px;
				padding: 20px;
				&:nth-child(1) {
					width: 280px;
				}
				&:nth-child(2) {
					width: 90px;
				}
				&:nth-child(3) {
					width: 80px;
				}
				&:nth-child(4) {
					width: 120px;
				}
			}
		}
		&_infor {
			a {
				display: inline-block;
				vertical-align: top;
				overflow: hidden;
			}
		}
		&_thumb {
			@include size(82px,82px);
			margin-right: 20px;
			border: 1px solid $border-color;
		}
		&_title {
			width: 120px;
			&:hover {
				color: $main-color;
			}
		}
		&_handle {
			a {
				display: block;
				margin-bottom: 5px;
				&:hover {
					color: $main-color;
				}
			}
		}
	}
}