.goods {
	&_box {
		// margin-top: 30px;
		margin-bottom: 12px;
		@extend .text_ellipsis;
	}

	&_box_top {
		padding: 20px 0;
		border-bottom: 1px solid $border-color;
	}

	&_box_title {
		position: relative;
		font-size: 16px;
		font-weight: bold;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -10px;
			@include size(40px, 2px);
			background-color: $main-color;
		}
	}

	&_box_kind {
		li {
			display: inline-block;
			font-size: 14px;
			margin-left: 16px;
			padding: 4px 0;
			cursor: pointer;

			&.on {
				color: $main-color;
				border-bottom: 2px solid $main-color;
			}
		}
	}

	&_box_container {
		@extend .clearfix;
	}

	&_box_ad {
		float: left;
		overflow: hidden;
		position: relative;
		@include size(290px, 550px);
		@include shadow;
	}

	&_box_right {
		width: 910px;
		float: left;
	}

	&_list {
		@extend .clearfix;		
	}

	&_item {
		position: relative;
		float: left;
		width: 213px;
		height: 268px;
		margin-bottom: 14px;
		margin-left: 14px;
		padding: 15px 0;
		background-color: $white-color;
		transition: all .6s;

		&:hover {
			box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
			transform: translate3d(0, -2px, 0);
		}

		&_pic {
			overflow: hidden;
			display: block;
			margin: 0 auto;
			@include size(160px, 160px);
		}

		&_title {
			margin-bottom: 6px;
			@include rowhide(2, 22px);
			font-size: 14px;

			&:hover {
				color: $main-color;
			}
		}

		&_wrap {
			padding: 0 12px;
		}

		&_classify {
			@extend .clearfix;
		}

		&_price {
			float: left;
			color: $main-color;

			strong {
				margin-left: 4px;
				font-size: 20px;
				font-weight: bold;
			}
		}

		&_btn {
			float: right;
			@include size(40px, 22px);
			line-height: 20px;
			font-size: 13px;
			text-align: center;
			color: $main-color;
			border: 1px solid $main-color;
			border-radius: 6px;
			cursor: pointer;
		}

		&_tips {
			@include position(0, 0);
			padding: 2px 8px;
			font-size: 12px;
			background-color: $main-color;
			color: $white-color;
			&.new {
				background-color: $main-color;
			}
			&.hot {
				background-color: #83c44e;
			}
		}
		&_close {
			display: none;
			opacity: 0;
			position: absolute;
			right: 10px;
			top: 6px;
			color: $ccc-color;
			cursor: pointer;
			&:hover {
				color: $main-color;
			}
		}
	}
}