$coupon-color: #ff773e;

.coupon {
	&_list {
		@extend .clearfix;
		padding-left: 10px;
		padding-top: 16px;
	}

	&_item {
		position: relative;
		float: left;
		@include size(210px, 110px);
		margin-right: 20px;
		margin-bottom: 20px;
		padding-bottom: 7px;
		font-size: 12px;
		color: $nine-color;
		background: url(../img/coupon_bg.png) no-repeat 0;
		cursor: pointer;

		&.active {
			background-image: url(../img/coupon_get_bg.png);
		}
	}

	&_txt {
		margin: 10px 0 0 10px;

		p {
			line-height: 24px;
		}
	}

	&_price {
		color: $coupon-color;

		span {
			font-size: 20px;
			margin-left: 4px;
			margin-right: 10px;
		}
	}

	&_bg {
		cursor: pointer;
		position: absolute;
		right: 0;
		width: 38px;
		height: 100%;
		background: transparent;
		top: 0;
		bottom: 0;
		margin: 0;
		padding: 0;
	}

	&_top {
		&_list {
			li {
				width: auto;
				margin-right: 30px;
			}
		}
	}

	&_wrap {
		&_unit {
			padding-left: 24px;
			padding-top: 24px;
		}
		
		&_item {			
			display: none;
			opacity: 0;

			&.active {
				display: block;
				opacity: 1;
			}
		}

		&_unit {
			@extend .clearfix;
		}

		&_cell {
			width: 204px;
			float: left;
			margin-right: 50px;
			margin-bottom: 40px;

			.top {
				position: relative;
				height: 120px;
				padding-top: 36px;
				text-align: center;
				background: #7ecfd0 url(../img/coupon_btm_bg.png) repeat-x bottom;
				&.gray {
					background: #afafaf url(../img/coupon_btm_get.png) repeat-x bottom;
					.range {
						color: $white-color;
					}
				}

				&:before {
					content: '';
					@include position(50%, -18px);
					@include size(36px, 36px);
					margin-left: -18px;
					border-radius: 50%;
					background-color: $white-color;
				}
				.price {
					margin-bottom: 10px;
					font-size: 34px;
					color: $white-color;
				}
	
				.condition {
					font-size: 13px;
					color: $white-color;
				}
	
				.range {
					font-size: 12px;
					color: #217f7f;
				}
	
				.sample {
					position: absolute;
					left: -29px;
					top: -7px;
					@include size(88px, 44px);
					border-bottom: 44px solid #fee833;
					border-left: 44px solid transparent;
					border-right: 44px solid transparent;
					transform: rotate(-45deg);
				}
	
				.status {
					@include position(1px, 11px);
					font-size: 13px;
					transform: rotate(-45deg);
				}
			}
			.btm {
				padding-left: 12px;
				line-height: 40px;
				height: 40px;
				color: $six-color;
				background-color: $bg-color;
			}
			
		}
	}
}