body {
    font-size: 14px;
    color: #333;
    font-family: "Segoe UI","Lucida Grande",Helvetica,Arial,"Microsoft YaHei",FreeSans,Arimo,"Droid Sans","wenquanyi micro hei","Hiragino Sans GB","Hiragino Sans GB W3",FontAwesome,sans-serif;
    line-height: 20px;
    background-color: #f5f5f5;
    -webkit-touch-callout: none;
    -webkit-font-smoothing: antialiased;
}

.float_lt {
    float: left;
}

.float_rt {
    float: right;
}

.clearfix:after {
    content: '';
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}

.clearfix {
    zoom: 1;
}

.text_ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.align_center {
    text-align: center;
}

.flex_center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: wrap;
}

.flex_around {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-flow: wrap;
}

.flex_between {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: wrap;
}

.flex_start {
    display: flex;
    align-items: center;
    flex-flow: wrap;
}

.flex_normal {
    display: flex;
    align-items: center;
}

.bg_cover {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.bg_contain {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

html {
	min-width: $common-width;
}

.common_wrap {
	width: $common-width;
	margin: 0 auto;
}
