.collect {
	&_box {
		padding: 0 20px;
	}
	&_top {
		position: relative;
		@include lineht(50px);
		border-bottom: 1px solid $border-color;
		h6 {
			font-size: 15px;
		}
	}
	&_list {
		@extend .clearfix;
		padding: 20px;
		.goods_item {
			border: 1px solid $border-color;
			.goods_item_close {
				display: block;
				opacity: 1;
			}
		}
	}
}