.center {
	&_menu {
		float: left;
		width: 116px;
		padding: 30px 0;
		background-color: $white-color;
		&_list {
			padding-left: 20px;
			li {
				padding: 6px 0;
				font-size: 12px;
				&.sub {
					font-size: 14px;
					font-weight: bold;
				}
				&.active,
				&:hover {
					color: $main-color;
				}
			}
		}
	}
	&_white {
		overflow: hidden;
		margin-top: 20px;
		background-color: $white-color;
	}
	&_right {
		position: relative;
		width: 1068px;
		min-height: 540px;
		overflow: hidden;
	}
	&_thumb {
		overflow: hidden;
		margin: 15px;
		@include size(90px,90px);
		border-radius: 50%;
	}
	&_infor {
		margin-top: 30px;
		h5 {
			margin-bottom: 20px;
			font-size: 18px;
			font-weight: bold;
			color: $four-color;
		}
		p {
			font-size: 12px;
			span {
				margin-left: 8px;
				color: $blue-color;
			}
		}
	}
	&_order {
		&_list {
			padding-top: 14px;
		}
		&_item {
			float: left;
			margin-left: 100px;
			text-align: center;
		}
		&_icon {
			display: block;
			margin: 0 auto;
			@include size(40px,40px);
			text-align: center;
			line-height: 40px;
			border-radius: 50%;
			background-color: lighten($blue-color,35%);
			i {
				font-size: 20px;
			}
		}
		&_title {
			font-size: 14px;
			line-height: 32px;
			color: $six-color;
			font-weight: bold;
			&:hover {
				color: $main-color;
			}
		}
		&_sum {
			color: $deputy-color;
		}
	}
	&_coupon {
		padding: 15px;
	}
	&_goods {
		@extend .clearfix;
		padding: 15px;
		.goods_item {
			border: 1px solid $bg-color;
		}
	}
}