.form {
	&_group {
		position: relative;
		margin-bottom: 20px;
	}
	&_ipt {
		@include size(100%,100%);
		height: 36px;
		line-height: 34px;
		padding-left: 38px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		background-color: rgba($color: #fff, $alpha: .2);
	}
	&_icon {
		@include position(1px,1px);
		@include size(34px,34px);
		text-align: center;
		line-height: 34px;
		.iconfont {
			font-size: 20px;
			color: #999;
		}
	}
	&_btn {
		@include size(100%,36px);
		text-align: center;
		@include colbg($white-color,$main-color);
		border-radius: 3px;
		&:hover {
			background-color: lighten($main-color,10%);
		}
	}
	&_login {
		color: $nine-color;
		a {
			color: $main-color;
			&:hover {
				color: darken($main-color,10%);
			}
		}
	}
	&_msg {
		@include size(100px,36px);
		line-height: 36px;
		text-align: center;
		border-radius: 3px;
		@include colbg($white-color,$deputy-color);
		cursor: pointer;
		&:disabled {
			background-color: lighten($deputy-color,30%);
			cursor: none;
		}
	}
	&_forget {
		color: $six-color;
		text-decoration: underline;
		&:hover {
			color: $main-color;
			text-decoration: underline;
		}
	}
	&_lb {
		position: relative;
		display: inline-block;
		width: 80px;
		margin-right: 20px;
		line-height: 40px;
		text-align: right;
		font-size: 14px;
		&.star {
			&:before {
				content: '*';
				display: inline-block;
				color: $main-color;
			}
		}
	}
	&_txt {
		width: 410px;
		display: inline-block;
		@include lineht(40px);
		font-size: 14px;
		input[type='text'] {
			@include size(100%,100%);
			text-indent: 12px;
			border: 1px solid $border-color;
			border-radius: 5px;
			background-color: $bg-color;
			@include placehold($nine-color,13px);
		}
	}
	&_sex {
		width: 60px;
		display: inline-block;
		input[type="radio"] {
			vertical-align: middle;
			margin-left: 8px;
		}
	}
	&_button {
		margin-right: 26px;
		button {
			float: right;
			margin-left: 20px;
			@include size(80px,40px);
			line-height: 38px;
			border-style: solid;
			border-width: 1px;
			border-radius: 5px;
			transition: all .2s ease-in-out;
		}
		&_submit {
			background-color: transparent;
			color: $blue-color;
			border-color: $blue-color;
			&:hover {
				background-color: $blue-color;
				color: $white-color;
				border-color: $blue-color;
			}
		}
		&_cancle {
			background-color: $bg-color;
			color: $six-color;
			border-color: $border-color;
		}
	}
}